.forecast-summary {
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px 5px;
  background-color: rgba(255, 255, 255, 0.7);
  font-size: 0.8rem;
  width: 16%;
  text-align: center;
}

.forecast-summary > button {
  border: none;
  font-size: 0.75rem;
  padding: 0px;
  margin: 5px;
  background-color: rgba(255, 255, 255, 0);
  font-family: sans-serif;
  outline: none;
  cursor: pointer;
}

.forecast-summary > div {
  margin: 3px;
}

.forecast-summary__icon {
  font-size: 35px;
  padding: px;
}

@media only screen and (max-width: 750px) {
  .forecast-summary {
    width: 16%;
    /* padding: 5px 4px; */
  }

  .forecast-summary > button {
    font-size: 0.5rem;
    margin: 1px;
  }

  .forecast-summary > div {
    margin: 1px;
  }

  .forecast-summary__icon {
    font-size: 20px;
    padding: 5px;
  }
}
